main {
	height: 300px;
	width: 100%;
	background-image: url("../../img/computer-1873831_1280.png");
	background-color: black !important;
	display: flex !important;
	align-items: center;
	text-align: center;
}

.description {
	margin: 0 1rem;
	padding: 0 2%;
}

h1 {
	background-color: rgba(0, 0, 0, 0.7);
	margin: 0 auto;
}

article li {
	list-style: square;
}

 
.card-img-top{
	width: 35% !important;
	margin: auto;
}

@media screen and (min-width: 768px) {
	main {
		text-align: center;
		background-image: url("../../img/computer-1873831_1920.png");
		height: 100vh;
		width: 100%;
	}

	h1 {
		transform: scale(2);
	}

	h3 {
		font-size: 3rem !important;
		color: black;
		text-align: center;
	}

	article {
		margin: 20px 0;
	}

	p {
		font-size: 200%;
	}

	li {
		font-size: 200%;
	}
}
