@import url(https://fonts.googleapis.com/css2?family=Arimo:wght@600&display=swap);
main {
	height: 300px;
	width: 100%;
	background-image: url(/portafolio-Abraham/static/media/computer-1873831_1280.47b5afb1.png);
	background-color: black !important;
	display: flex !important;
	align-items: center;
	text-align: center;
}

.description {
	margin: 0 1rem;
	padding: 0 2%;
}

h1 {
	background-color: rgba(0, 0, 0, 0.7);
	margin: 0 auto;
}

article li {
	list-style: square;
}

 
.card-img-top{
	width: 35% !important;
	margin: auto;
}

@media screen and (min-width: 768px) {
	main {
		text-align: center;
		background-image: url(/portafolio-Abraham/static/media/computer-1873831_1920.0c9d7948.png);
		height: 100vh;
		width: 100%;
	}

	h1 {
		-webkit-transform: scale(2);
		        transform: scale(2);
	}

	h3 {
		font-size: 3rem !important;
		color: black;
		text-align: center;
	}

	article {
		margin: 20px 0;
	}

	p {
		font-size: 200%;
	}

	li {
		font-size: 200%;
	}
}

.list-group {
	color: white;
}

.list-group-item {
    border: 1px white solid !important;
	background-color: var(--secondary-color) !important;
}


.u-list a {
	background-image: linear-gradient(var(--secondary-color), var(--secondary-color));
	background-position: bottom;
	background-repeat: no-repeat;
	background-size: 0 3px;
	transition: all 200ms linear;
}

 .section-item:hover {
	background-size: 100% 3px;
}

p {
	margin: 0 !important;
}

@media screen and (min-width: 768px) {

	.data-icon {
		display: none;
	}
	.section-item {
		margin: 0 2rem;
	}

	.section-item li {
		display: flex;
		align-items: center;
	}

	.section-item p{
		font-size: 1rem;
		transition: all 400ms ease;
	}
	.section-item p:hover{
		-webkit-transform: scale(1.4);
		        transform: scale(1.4);
	}
	.section-item:hover {
		background-size: 0% 3px;
	}

	a {
		display: flex;
		justify-content: center;
		align-items: center;
	}
}


.bg-black {
	background-color: var(--primary-color) !important;
}

.btn-info {
	background: var(--secondary-color) !important;
}


.navbar-brand a {
	font-size: 1.3rem;
}
a {
	color: white !important;
	text-decoration: none;
}

a:hover {
	color: white;
	text-decoration: none !important;
	text-decoration: none;
}

li {
	list-style: none;
}

.navbar-toggler {
	padding: 0 !important;
	font-size: 1.1rem;
}

.navbar {
	padding: 0.5rem !important;
}

.u-list {
	margin: 0.2rem 0.5rem;
	padding: 0;
}

@media screen and (max-width: 330px) {
	.break {
		display: block;
	}
}

@media screen and (min-width: 768px) {
	.navbar-brand {
		font-size: 1.5rem !important;
	}
}



.btn-contact {
    background-color: var(--info-color) !important;
}


#contact-section {
    position: fixed;
    background-color: var(--primary-color);
    top: 0;
    right: 0;
    width: 70%;
    height: 100vh;
    text-align: center;
    color: white;
    border: white 1px solid;
    padding: 3%;
}

.contact-section img {
    width: 120px;
    border-radius: 50%;
}

#close {
    position: absolute;
    left: -40px;
    top: 10px;
    width: 30px;
    height: 30px;
    background-color: white;
    color: black !important;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (min-width: 768px) {
    #contact-section {
        width: 30%;
    }
}

:root {
	--primary-color: #282a39;
	--secondary-color: #af3b31;
  --info-color: #af3b31;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

section {
  margin: 0;
  padding: 1% 0;
}

article {
  margin: 20px 0 !important;
  padding: 2%;
  background-color: white;
}

.content {
  background-color: #282a39 !important;
  background-color: var(--primary-color) !important;
  color: #282a39 !important;
  color: var(--primary-color) !important;
  font-family: 'Arimo', sans-serif;
  height: 100vh;
}








.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}




body {
	margin: 0;
	margin-bottom: 40px;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
		"Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#button-icon {
	position: fixed;
	bottom: 40px;
	right: 20px;
	z-index: 10;
}

