

.btn-contact {
    background-color: var(--info-color) !important;
}


#contact-section {
    position: fixed;
    background-color: var(--primary-color);
    top: 0;
    right: 0;
    width: 70%;
    height: 100vh;
    text-align: center;
    color: white;
    border: white 1px solid;
    padding: 3%;
}

.contact-section img {
    width: 120px;
    border-radius: 50%;
}

#close {
    position: absolute;
    left: -40px;
    top: 10px;
    width: 30px;
    height: 30px;
    background-color: white;
    color: black !important;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (min-width: 768px) {
    #contact-section {
        width: 30%;
    }
}
